export default {
  data() {
    return {
      user: {
        username: '',
        password: ''
      }
    }
  },
  methods: {
    login() {
      this.$store.commit('auth/setUser', this.user.username)
      this.$store.commit('auth/setPass', this.user.password)
      this.$router.push({ name: 'named-middleware' })
    }
  }
}
